import { createRouter, createWebHistory } from 'vue-router'
import { nextTick } from 'vue';


const HomeView = () => import('@/views/HomeView.vue');
const EventView = () => import('@/views/EventView.vue');
const TeamView = () => import('@/views/TeamView.vue');
const AboutView = () => import('@/views/AboutView.vue');
const GalleryView = () => import('@/views/GalleryView.vue');

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView,
  },
  {
    path: '/event/:id',
    name: 'EventView',
    component: EventView,
    props: route => ({ id: route.params.id, eventData: route.query.eventData || null }),
  },
  {
    path: '/gallery',
    name: 'GalleryView',
    component: GalleryView,
  },
  {
    path: '/team/:id',
    name: 'TeamView',
    component: TeamView,
    props:true
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
        return savedPosition; // For back/forward navigation
    } else {
        nextTick(() => {
            window.scrollTo(0, 0); // Scroll to the top after the content is rendered
        });
        return { x: 0, y: 0 }; // Just in case
    }
}
})

export default router
